import { Slot } from '@radix-ui/react-slot';
import { ReactNode } from 'react';

export interface ButtonProps {
	children: ReactNode;
	asChild?: boolean;
}

export function Button({ children, asChild }: ButtonProps) {
	const Component = asChild ? Slot : 'button';

	return (
		<Component className="text-black py-4 px-3 bg-cyan-500 hover:bg-cyan-300 rounded font-semibold text-sm w-full outline-none focus:ring-2 ring-white">
			{children}
		</Component>
	);
}
